import Nav from "../components/Nav";
import Home from "./Home";
import Members from "./Members";
import Forum from "./Forum";
import BookUsOnline from "./BookUsOnline";
import OurBlog from "./OurBlog";
import Footer from "../components/Footer";

import { Route, Switch, useLocation } from "react-router-dom";
import { useEffect } from "react";
import { AnimatePresence } from "framer-motion";

function WholePage() {
	const location = useLocation();
	useEffect(() => {
		window.scrollTo(0, 0);
		document.getElementById("/").style.borderBottomWidth = "0px";
		document.getElementById("/Members").style.borderBottomWidth = "0px";
		document.getElementById("/Forum").style.borderBottomWidth = "0px";
		document.getElementById("/BookUsOnline").style.borderBottomWidth =
			"0px";
		document.getElementById("/OurBlog").style.borderBottomWidth = "0px";
		try {
			document.getElementById(location.pathname).style.borderBottomWidth =
				"thin";
		} catch {
			console.log("Invalid Page Request");
		}
	}, [location]);

	window.addEventListener("scroll", () => {
		try {
			if (location.pathname === "/") {
				document.getElementById("sectionP").style.height = `${
					611 - window.scrollY
				}px`;
				document.getElementById("headPL").style.top = `${
					330 - window.scrollY / 1.39
				}px`;
			}
		} catch {}
	});

	return (
		<>
			<Nav />
			<AnimatePresence exitBeforeEnter>
				<Switch location={location} key={location.pathname}>
					<Route path="/SehMat/Members" exact>
						<Members />
						<Footer />
					</Route>
					<Route path="/SehMat/Forum" exact>
						<Forum />
						<Footer />
					</Route>
					<Route path="/SehMat/BookUsOnline" exact>
						<BookUsOnline />
						<Footer />
					</Route>
					<Route path="/SehMat/OurBlog" exact>
						<OurBlog />
						<Footer />
					</Route>
					<Route path="/SehMat/:id">
						<h1
							style={{
								color: "white",
								height: "10vh",
								textAlign: "center",
								padding: "30vh",
							}}
						>
							404: Page not found
						</h1>
						<Footer />
					</Route>
					<Route path="/SehMat/" exact>
						<Home />
						<Footer />
					</Route>
				</Switch>
			</AnimatePresence>
		</>
	);
}

export default WholePage;
