import styled from "styled-components";
import { motion } from "framer-motion";
import {fadeY,zoomOut} from '../animation'

import { useScroll } from "./UseScroll";

function Member({ member }) {
	const [element, controls] = useScroll();
	return (
		<StyledMember
			variants={fadeY}
			ref={element}
			animate={controls}
			initial={"hidden"}
		>
			<motion.h4 variants={fadeY}>{member[0]}</motion.h4>
			<motion.img variants={zoomOut} src={member[1]} alt={member[0]} />
			<motion.p
				variants={fadeY}
				custom={39}
			>
				<q>
					{member[2].map((t) =>
						t === member[2][member[2].length - 1] ? (
							<>{t}</>
						) : (
							<>
								{t}
								<br />
							</>
						)
					)}
				</q>
			</motion.p>
		</StyledMember>
	);
}

const StyledMember = styled(motion.div)`
	width: 338.66px;
	margin-top: 40px;
	display: flex;
	flex-direction: column;
	font-family: "Lato", arial, sans-serif;

	h4 {
		color: rgb(226, 226, 226);
		font-size: 23px;
		text-align: left;
		font-weight: 300;
		overflow-wrap: break-word;
		line-height: 1.25;
		margin-bottom: 32px;
		height: 46px;
	}

	img {
		border-radius: 50%;
		margin-bottom: 24px;
		align-self: center;
		height: 338.66px;
		width: 338.66px;
		object-fit: cover;
		background: white;
	}

	p {
		font-size: 16px;
		font-weight: 300;
		color: rgb(145, 145, 145);
		font-style: italic;
		text-align: left;
		line-height: 1.5;
		width: 100%;
		align-self: center;
	}
`;

export default Member;
