export const pageAnimation = {
	hidden: {
		opacity: 0,
		x: 500,
		// scaleX: 0.3,
		transition: { duration: 0.43 },
	},
	show: (i = 0.29) => ({
		opacity: 1,
		x: 0,
		// scaleX: 1,
		transition: {
			duration: 0.29,
			// delay: 0.1,
			ease: "easeOut",
			when: "beforeChildren",
			staggerChildren: i,
		},
	}),
	exit: {
		opacity: 0,
		x: -500,
		// scaleX: 0.3,
		transition: { duration: 0.43 },
	},
};

