import "./styles/App.scss";
import Intro from "./components/Intro";
import { useState } from "react";
import WholePage from "./pages/WholePage";

function App() {
	const [introState, setIntroState] = useState(false);

	return (
		<div className="App">
			{introState ? <Intro setIntroState={setIntroState} /> : <WholePage />}
		</div>
	);
}

export default App;
