import styled from "styled-components";
import { motion } from "framer-motion";

function Button({ text, classT = "", onclickToggle = function () {} }) {
	return (
		<StyledButton onClick={onclickToggle} className={classT}>
			{text}
		</StyledButton>
	);
}

const StyledButton = styled(motion.Button)`
	height: 56px;
	cursor: pointer;
	font-weight: 500;
	font-size: 16px;
	padding: 8px 32px;
	border-radius: 0;
	letter-spacing: 0.063em;
	background: transparent;
	white-space: nowrap;
	color: rgb(195, 160, 36);
	border: 1px solid rgb(195, 160, 36);
	transition: 0.3s;

	&:hover {
		color: black;
		background: rgb(195, 160, 36);
	}
`;

export default Button;
